/* ============================================
   VS CODE STYLE PROJECT MODAL - ENHANCED
   Ultra-realistic with smooth animations
   ============================================ */

/* ============================================
   CSS VARIABLES
   ============================================ */
:root {
  /* VS Code Dark Theme Colors */
  --vscode-bg: #1e1e1e;
  --vscode-sidebar: #252526;
  --vscode-activitybar: #333333;
  --vscode-titlebar: #323233;
  --vscode-statusbar: #007acc;
  --vscode-accent: #007acc;
  --vscode-accent-hover: #1a8ad4;
  --vscode-border: #3c3c3c;
  --vscode-text: #cccccc;
  --vscode-text-muted: #858585;
  --vscode-text-bright: #ffffff;
  --vscode-selection: #264f78;
  --vscode-hover: #2a2d2e;
  --vscode-active: #094771;

  /* Syntax Colors */
  --syntax-keyword: #569cd6;
  --syntax-string: #ce9178;
  --syntax-function: #dcdcaa;
  --syntax-variable: #9cdcfe;
  --syntax-comment: #6a9955;
  --syntax-number: #b5cea8;

  /* Status Colors */
  --color-success: #4caf50;
  --color-warning: #ffb74d;
  --color-error: #f44336;
  --color-info: #4fc3f7;

  /* Transitions */
  --transition-fast: 0.15s ease;
  --transition-normal: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-smooth: 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);

  /* Shadows */
  --shadow-modal: 0 25px 100px rgba(0, 0, 0, 0.6),
    0 10px 40px rgba(0, 0, 0, 0.4);
  --shadow-dropdown: 0 8px 30px rgba(0, 0, 0, 0.3);
  --shadow-glow: 0 0 30px rgba(0, 122, 204, 0.3);
}

/* ============================================
     OVERLAY
     ============================================ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 20px;
  animation: overlayFadeIn 0.3s ease;
}

@keyframes overlayFadeIn {
  from {
    opacity: 0;
    backdrop-filter: blur(0);
  }
  to {
    opacity: 1;
    backdrop-filter: blur(8px);
  }
}

/* ============================================
     MAIN MODAL CONTAINER
     ============================================ */
.vscode-modal {
  width: 95%;
  max-width: 1400px;
  height: 90vh;
  max-height: 900px;
  background: var(--vscode-bg);
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: var(--shadow-modal);
  animation: modalSlideUp 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  border: 1px solid var(--vscode-border);
  position: relative;
  /* ✅ ADD THESE LINES TO STOP FLICKERING */
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
  will-change: transform, opacity;
}

@keyframes modalSlideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Modal closing animation */
.vscode-modal.closing {
  animation: modalSlideDown 0.3s ease forwards;
}

@keyframes modalSlideDown {
  to {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
}

/* ============================================
     TITLE BAR - macOS Style
     ============================================ */
.vscode-titlebar {
  height: 40px;
  background: linear-gradient(180deg, #3c3c3c 0%, var(--vscode-titlebar) 100%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  border-bottom: 1px solid #1a1a1a;
  user-select: none;
  flex-shrink: 0;
}

.titlebar-left {
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--vscode-text);
  font-size: 13px;
  font-weight: 500;
}

.vscode-icon {
  color: var(--vscode-accent);
  font-size: 18px;
  filter: drop-shadow(0 0 5px rgba(0, 122, 204, 0.5));
}

.titlebar-right {
  display: flex;
  gap: 8px;
}

.titlebar-btn {
  width: 32px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  color: var(--vscode-text);
  cursor: pointer;
  border-radius: 5px;
  transition: var(--transition-fast);
  text-decoration: none;
  font-size: 14px;
}

.titlebar-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.titlebar-btn.close:hover {
  background: #e81123;
  color: white;
}

/* Traffic Lights (macOS style) */
.titlebar-traffic-lights {
  display: flex;
  gap: 15px;
  margin-right: 15px;
}

.traffic-light {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
  transition: var(--transition-fast);
  position: relative;
}
/* ✅ 4. INVISIBLE TOUCH TARGET EXPANSION 
     This creates a 40px clickable area around the 20px dot 
     without changing the visual design */
.traffic-light::after {
  content: "";
  position: absolute;
  top: -10px;
  bottom: -10px;
  left: -10px;
  right: -10px;
  z-index: 10;
}

.traffic-light.red::before {
  content: "×";
  opacity: 1; /* Visible by default */
  color: #4d0000;
  font-size: 16px; /* Larger text */
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.traffic-light::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 50%;
  opacity: 0;
  transition: var(--transition-fast);
}

.traffic-light.red {
  background: #ff5f56;
  border: 1px solid #e0443e;
}

.traffic-light.yellow {
  background: #ffbd2e;
  border: 1px solid #dea123;
}

.traffic-light.green {
  background: #27c93f;
  border: 1px solid #1aab29;
}

.vscode-titlebar:hover .traffic-light.red::before {
  content: "×";
  opacity: 1;
  color: #4d0000;
  font-size: 10px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.vscode-titlebar:hover .traffic-light.yellow::before {
  content: "−";
  opacity: 1;
  color: #995700;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.vscode-titlebar:hover .traffic-light.green::before {
  content: "+";
  opacity: 1;
  color: #006500;
  font-size: 10px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ============================================
     MAIN LAYOUT
     ============================================ */
.vscode-main {
  flex: 1;
  display: flex;
  overflow: hidden;
}

/* ============================================
     ACTIVITY BAR (Left Icons)
     ============================================ */
.vscode-activitybar {
  width: 50px;
  background: var(--vscode-activitybar);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px 0;
  gap: 4px;
  border-right: 1px solid var(--vscode-bg);
  flex-shrink: 0;
}

.activity-icon {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--vscode-text-muted);
  font-size: 24px;
  cursor: pointer;
  position: relative;
  transition: var(--transition-fast);
  border-radius: 5px;
}

.activity-icon:hover {
  color: var(--vscode-text-bright);
}

.activity-icon.active {
  color: var(--vscode-text-bright);
}

.activity-icon.active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 2px;
  height: 24px;
  background: var(--vscode-text-bright);
  border-radius: 0 2px 2px 0;
}

/* Activity Badge */
.activity-icon .badge {
  position: absolute;
  top: 8px;
  right: 8px;
  min-width: 16px;
  height: 16px;
  background: var(--vscode-accent);
  color: white;
  font-size: 10px;
  font-weight: 600;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 4px;
}

.terminal-icon {
  margin-top: auto;
  margin-bottom: 8px;
}

/* ============================================
     SIDEBAR (File Explorer)
     ============================================ */
.vscode-sidebar {
  width: 260px;
  background: var(--vscode-sidebar);
  border-right: 1px solid var(--vscode-border);
  overflow-y: auto;
  overflow-x: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}

/* Custom Scrollbar */
.vscode-sidebar::-webkit-scrollbar {
  width: 10px;
}

.vscode-sidebar::-webkit-scrollbar-track {
  background: transparent;
}

.vscode-sidebar::-webkit-scrollbar-thumb {
  background: rgba(121, 121, 121, 0.4);
  border-radius: 5px;
  border: 2px solid transparent;
  background-clip: content-box;
}

.vscode-sidebar::-webkit-scrollbar-thumb:hover {
  background: rgba(121, 121, 121, 0.7);
  background-clip: content-box;
}

.sidebar-header {
  padding: 12px 20px;
  font-size: 11px;
  font-weight: 600;
  color: var(--vscode-text-muted);
  letter-spacing: 1px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--vscode-border);
  flex-shrink: 0;
}

.sidebar-header-actions {
  display: flex;
  gap: 5px;
}

.sidebar-header-btn {
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  border-radius: 3px;
  font-size: 14px;
  transition: var(--transition-fast);
}

.sidebar-header-btn:hover {
  background: var(--vscode-hover);
  color: var(--vscode-text);
}

.sidebar-section {
  padding: 8px 0;
  flex: 1;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  font-size: 11px;
  font-weight: 700;
  color: var(--vscode-text);
  cursor: pointer;
  letter-spacing: 0.5px;
  transition: var(--transition-fast);
}

.section-title:hover {
  background: var(--vscode-hover);
}

.section-title svg {
  font-size: 12px;
  transition: transform var(--transition-fast);
}

.section-title.collapsed svg {
  transform: rotate(-90deg);
}

/* ============================================
     FILE TREE
     ============================================ */
.file-tree {
  padding-left: 8px;
}

.tree-folder {
  user-select: none;
}

.tree-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 5px 12px;
  font-size: 13px;
  color: var(--vscode-text);
  cursor: pointer;
  border-radius: 4px;
  margin: 1px 4px;
  transition: var(--transition-fast);
  position: relative;
}

.tree-item:hover {
  background: var(--vscode-hover);
}

.tree-item.active {
  background: var(--vscode-active);
}

.tree-item.active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--vscode-accent);
}

.tree-item.folder {
  font-weight: 500;
}

.tree-item svg {
  font-size: 16px;
  flex-shrink: 0;
}

.folder-icon {
  color: #dcb67a;
}

.folder-icon.open {
  color: #e8ab53;
}

.tree-children {
  padding-left: 16px;
  position: relative;
}

/* Tree Indent Guides */
.tree-children::before {
  content: "";
  position: absolute;
  left: 12px;
  top: 0;
  bottom: 8px;
  width: 1px;
  background: var(--vscode-border);
}

/* File Icons by Type */
.file-icon-md {
  color: #519aba;
}
.file-icon-json {
  color: #cbcb41;
}
.file-icon-js {
  color: #f7df1e;
}
.file-icon-py {
  color: #3572a5;
}
.file-icon-ts {
  color: #3178c6;
}
.file-icon-css {
  color: #563d7c;
}
.file-icon-html {
  color: #e34c26;
}

/* ============================================
     EDITOR AREA
     ============================================ */
.vscode-editor {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: var(--vscode-bg);
  min-width: 0; /* Important for flex shrinking */
  padding-right: 6px;
}

/* ============================================
     EDITOR TABS
     ============================================ */
.editor-tabs {
  display: flex;
  background: var(--vscode-sidebar);
  border-bottom: 1px solid var(--vscode-border);
  overflow-x: auto;
  flex-shrink: 0;
  scrollbar-width: thin;
}

.editor-tabs::-webkit-scrollbar {
  height: 3px;
}

.editor-tabs::-webkit-scrollbar-thumb {
  background: var(--vscode-border);
  border-radius: 3px;
}

.editor-tab {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 15px;
  font-size: 13px;
  color: var(--vscode-text-muted);
  cursor: pointer;
  border-right: 1px solid var(--vscode-border);
  background: #2d2d2d;
  transition: var(--transition-fast);
  white-space: nowrap;
  position: relative;
  min-width: 0;
}

.editor-tab:hover {
  background: #323232;
  color: var(--vscode-text);
}

.editor-tab.active {
  background: var(--vscode-bg);
  color: var(--vscode-text-bright);
}

.editor-tab.active::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--vscode-accent);
}

.tab-icon {
  font-size: 14px;
  flex-shrink: 0;
}

.tab-text {
  overflow: hidden;
  text-overflow: ellipsis;
}

.tab-close {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  font-size: 16px;
  opacity: 0;
  transition: var(--transition-fast);
  margin-left: auto;
  flex-shrink: 0;
}

.editor-tab:hover .tab-close,
.editor-tab.active .tab-close {
  opacity: 0.7;
}

.tab-close:hover {
  background: rgba(255, 255, 255, 0.1);
  opacity: 1 !important;
}

/* Modified Indicator */
.tab-modified {
  width: 8px;
  height: 8px;
  background: var(--vscode-text-muted);
  border-radius: 50%;
  margin-left: 5px;
}

/* ============================================
     BREADCRUMB
     ============================================ */
.editor-breadcrumb {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 20px;
  font-size: 12px;
  color: var(--vscode-text-muted);
  background: var(--vscode-bg);
  border-bottom: 1px solid var(--vscode-border);
  flex-shrink: 0;
  overflow-x: auto;
}

.editor-breadcrumb::-webkit-scrollbar {
  display: none;
}

.breadcrumb-item {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 2px 6px;
  border-radius: 4px;
  cursor: pointer;
  transition: var(--transition-fast);
  white-space: nowrap;
}

.breadcrumb-item:hover {
  background: var(--vscode-hover);
  color: var(--vscode-text);
}

.breadcrumb-separator {
  color: var(--vscode-text-muted);
  font-size: 10px;
}

/* ============================================
     EDITOR CONTENT
     ============================================ */
.editor-content {
  flex: 1;
  padding: 30px 40px;
  overflow-y: auto;
  background: var(--vscode-bg);
  color: var(--vscode-text);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  line-height: 1.7;
  scroll-behavior: smooth;
}

/* Custom Scrollbar for Content */
.editor-content::-webkit-scrollbar {
  width: 10px;
}

.editor-content::-webkit-scrollbar-track {
  background: transparent;
}

.editor-content::-webkit-scrollbar-thumb {
  background: rgba(121, 121, 121, 0.4);
  border: 3px solid var(--vscode-bg);
  border-radius: 7px;
}

.editor-content::-webkit-scrollbar-thumb:hover {
  background: rgba(121, 121, 121, 0.7);
}

/* ============================================
     CONTENT TYPOGRAPHY
     ============================================ */
.editor-content h1 {
  font-size: 32px;
  font-weight: 600;
  color: var(--vscode-text-bright);
  margin: 0 0 20px 0;
  padding-bottom: 15px;
  border-bottom: 2px solid var(--vscode-border);
  display: flex;
  align-items: center;
  gap: 12px;
}

.editor-content h1 .emoji {
  font-size: 28px;
}

.editor-content h2 {
  font-size: 22px;
  font-weight: 600;
  color: var(--color-info);
  margin: 35px 0 18px 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

.editor-content h2::before {
  content: "##";
  color: var(--vscode-text-muted);
  font-weight: 400;
  font-size: 18px;
}

.editor-content h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--vscode-text-bright);
  margin: 25px 0 12px 0;
}

.editor-content p {
  color: var(--vscode-text);
  margin-bottom: 16px;
  font-size: 15px;
}

.editor-content ul,
.editor-content ol {
  padding-left: 25px;
  margin-bottom: 20px;
}

.editor-content li {
  margin-bottom: 10px;
  color: #b5b5b5;
  font-size: 14px;
  position: relative;
}

.editor-content li::marker {
  color: var(--vscode-accent);
}

/* ============================================
     PROJECT TAGLINE
     ============================================ */
.project-tagline {
  font-size: 18px;
  color: var(--vscode-text-muted);
  font-style: italic;
  margin-bottom: 25px;
  padding: 15px 20px;
  background: linear-gradient(
    90deg,
    rgba(0, 122, 204, 0.1) 0%,
    transparent 100%
  );
  border-left: 3px solid var(--vscode-accent);
  border-radius: 0 8px 8px 0;
}

/* ============================================
     BADGES
     ============================================ */
.badges {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 20px 0;
}

.badge {
  padding: 6px 14px;
  background: linear-gradient(135deg, #264f78 0%, #1e3a5f 100%);
  color: var(--syntax-variable);
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  border: 1px solid rgba(86, 156, 214, 0.3);
  transition: var(--transition-fast);
}

.badge:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(0, 122, 204, 0.3);
}

/* ============================================
     INFO TABLE
     ============================================ */
.info-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin: 20px 0;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.info-table td {
  padding: 14px 18px;
  border: 1px solid var(--vscode-border);
  font-size: 14px;
}

.info-table tr:first-child td {
  border-top: none;
}

.info-table tr:last-child td {
  border-bottom: none;
}

.info-table td:first-child {
  width: 140px;
  font-weight: 600;
  color: var(--syntax-variable);
  background: var(--vscode-sidebar);
  border-left: none;
}

.info-table td:last-child {
  background: rgba(0, 0, 0, 0.2);
  border-right: none;
}

/* ============================================
     PROBLEM & SOLUTION BOXES
     ============================================ */
.problem-box {
  background: linear-gradient(
    135deg,
    rgba(244, 67, 54, 0.15) 0%,
    rgba(244, 67, 54, 0.05) 100%
  );
  border-left: 4px solid var(--color-error);
  padding: 25px;
  border-radius: 0 12px 12px 0;
  margin: 20px 0;
  position: relative;
  overflow: hidden;
}

.problem-box::before {
  content: "⚠️";
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 24px;
  opacity: 0.5;
}

.solution-box {
  background: linear-gradient(
    135deg,
    rgba(76, 175, 80, 0.15) 0%,
    rgba(76, 175, 80, 0.05) 100%
  );
  border-left: 4px solid var(--color-success);
  padding: 25px;
  border-radius: 0 12px 12px 0;
  margin: 20px 0;
  position: relative;
  overflow: hidden;
}

.solution-box::before {
  content: "✅";
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 24px;
  opacity: 0.5;
}

.problem-box p,
.solution-box p {
  margin: 0;
  font-size: 15px;
  line-height: 1.8;
}

/* ============================================
     PAIN POINTS
     ============================================ */
.pain-points {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 12px;
}

.pain-points li {
  padding: 15px 20px;
  background: linear-gradient(
    135deg,
    rgba(244, 67, 54, 0.1) 0%,
    transparent 100%
  );
  border-radius: 10px;
  border: 1px solid rgba(244, 67, 54, 0.2);
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: var(--transition-fast);
}

.pain-points li:hover {
  transform: translateX(5px);
  border-color: rgba(244, 67, 54, 0.4);
}

/* ============================================
     ARCHITECTURE FLOW
     ============================================ */
.architecture-flow {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin: 35px 0;
  flex-wrap: wrap;
  padding: 30px;
  background: linear-gradient(
    135deg,
    rgba(0, 122, 204, 0.1) 0%,
    transparent 100%
  );
  border-radius: 15px;
  border: 1px solid var(--vscode-border);
}

.flow-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 25px 35px;
  background: linear-gradient(
    135deg,
    var(--vscode-selection) 0%,
    rgba(38, 79, 120, 0.5) 100%
  );
  border-radius: 15px;
  min-width: 140px;
  border: 1px solid rgba(86, 156, 214, 0.3);
  transition: var(--transition-normal);
}

.step-number {
  width: 40px;
  height: 40px;
  background: linear-gradient(
    135deg,
    var(--color-info) 0%,
    var(--vscode-accent) 100%
  );
  color: var(--vscode-bg);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 18px;
  box-shadow: 0 4px 15px rgba(79, 195, 247, 0.4);
}

.flow-step span:last-child {
  font-weight: 500;
  color: var(--vscode-text-bright);
}

.flow-arrow {
  font-size: 28px;
  color: var(--color-info);
  animation: arrowPulse 1.5s ease-in-out infinite;
}

@keyframes arrowPulse {
  0%,
  100% {
    opacity: 0.5;
    transform: translateX(0);
  }
  50% {
    opacity: 1;
    transform: translateX(5px);
  }
}

/* ============================================
     CODE BLOCKS
     ============================================ */
.code-block {
  background: #0d0d0d;
  border-radius: 12px;
  overflow: hidden;
  margin: 20px 0;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  border: 1px solid var(--vscode-border);
}

.code-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 18px;
  background: var(--vscode-sidebar);
  border-bottom: 1px solid var(--vscode-border);
}

.code-header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.code-filename {
  font-size: 13px;
  color: var(--vscode-text);
  font-family: "Fira Code", "Consolas", monospace;
}

.code-lang-badge {
  padding: 2px 8px;
  background: rgba(0, 122, 204, 0.2);
  color: var(--color-info);
  font-size: 10px;
  border-radius: 4px;
  text-transform: uppercase;
}

.code-header button {
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  padding: 6px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  transition: var(--transition-fast);
}

.code-header button:hover {
  background: var(--vscode-hover);
  color: var(--vscode-text-bright);
}

.code-header button.copied {
  color: var(--color-success);
}

.code-block pre {
  padding: 20px;
  margin: 0;
  overflow-x: auto;
  font-family: "Fira Code", "Consolas", "Monaco", monospace;
  font-size: 14px;
  line-height: 1.6;
  tab-size: 2;
}

.code-block code {
  color: var(--vscode-text);
}

/* Line Numbers */
.code-line-numbers {
  display: flex;
  flex-direction: column;
  padding-right: 15px;
  border-right: 1px solid var(--vscode-border);
  margin-right: 15px;
  color: var(--vscode-text-muted);
  user-select: none;
}

/* ============================================
     TECH STACK
     ============================================ */
.techstack-json {
  position: relative;
}

.techstack-json pre {
  background: linear-gradient(135deg, #0d0d0d 0%, #1a1a1a 100%);
  padding: 25px;
  border-radius: 12px;
  font-family: "Fira Code", monospace;
  font-size: 14px;
  color: var(--syntax-string);
  overflow-x: auto;
  border: 1px solid var(--vscode-border);
  line-height: 1.8;
}

.tech-categories {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 25px;
  margin-top: 30px;
}

.tech-category {
  background: linear-gradient(
    135deg,
    var(--vscode-sidebar) 0%,
    rgba(37, 37, 38, 0.5) 100%
  );
  padding: 20px;
  border-radius: 12px;
  border: 1px solid var(--vscode-border);
  transition: var(--transition-normal);
}

.tech-category:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  border-color: var(--vscode-accent);
}

.tech-category h3 {
  font-size: 13px;
  color: var(--color-info);
  margin: 0 0 15px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.tech-category h3::before {
  content: "";
  width: 8px;
  height: 8px;
  background: var(--color-info);
  border-radius: 50%;
}

.tech-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tech-badge {
  padding: 6px 14px;
  background: rgba(255, 255, 255, 0.05);
  color: var(--vscode-text);
  border-radius: 8px;
  font-size: 13px;
  border: 1px solid var(--vscode-border);
  transition: var(--transition-fast);
}

.tech-badge:hover {
  background: rgba(0, 122, 204, 0.2);
  border-color: var(--vscode-accent);
  color: var(--vscode-text-bright);
}

/* ============================================
     IMPACT GRID
     ============================================ */
.impact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin: 25px 0;
}

.impact-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 22px;
  background: linear-gradient(
    135deg,
    rgba(76, 175, 80, 0.12) 0%,
    rgba(76, 175, 80, 0.04) 100%
  );
  border-radius: 12px;
  border-left: 4px solid var(--color-success);
  transition: var(--transition-normal);
  cursor: default;
}

.impact-card:hover {
  transform: translateX(8px);
  box-shadow: 0 8px 25px rgba(76, 175, 80, 0.15);
}

.impact-icon {
  font-size: 24px;
  color: var(--color-success);
  flex-shrink: 0;
}

.impact-card span {
  font-size: 15px;
  font-weight: 500;
  color: var(--vscode-text-bright);
}

/* ============================================
     SCREENSHOTS
     ============================================ */
.screenshots-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 25px;
  margin: 25px 0;
}

.screenshot-card {
  background: var(--vscode-sidebar);
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid var(--vscode-border);
  transition: var(--transition-normal);
}

.screenshot-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
  border-color: var(--vscode-accent);
}

.screenshot-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: var(--transition-normal);
}

.screenshot-card:hover img {
  transform: scale(1.05);
}

.screenshot-card span {
  display: block;
  padding: 15px;
  font-size: 14px;
  color: var(--vscode-text-muted);
  font-weight: 500;
  border-top: 1px solid var(--vscode-border);
}

/* Placeholder Screens */
.placeholder-screenshots,
.placeholder-demo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 40px;
  background: linear-gradient(
    135deg,
    var(--vscode-sidebar) 0%,
    rgba(37, 37, 38, 0.5) 100%
  );
  border-radius: 15px;
  text-align: center;
  grid-column: 1 / -1;
  border: 2px dashed var(--vscode-border);
}

.placeholder-icon {
  font-size: 64px;
  color: var(--vscode-text-muted);
  margin-bottom: 20px;
  opacity: 0.5;
}

.placeholder-screenshots p,
.placeholder-demo p {
  color: var(--vscode-text-muted);
  margin-bottom: 20px;
}

.placeholder-screenshots a,
.placeholder-demo a {
  margin-top: 10px;
  color: var(--color-info);
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: rgba(0, 122, 204, 0.1);
  border: 1px solid var(--vscode-accent);
  border-radius: 8px;
  transition: var(--transition-fast);
}

.placeholder-screenshots a:hover,
.placeholder-demo a:hover {
  background: var(--vscode-accent);
  color: white;
}

/* ============================================
     VIDEO CONTAINER
     ============================================ */
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  margin: 25px 0;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid var(--vscode-border);
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* ============================================
     TERMINAL
     ============================================ */
.content-terminal {
  background: #0c0c0c;
  border-radius: 12px;
  overflow: hidden;
  margin: 20px 0;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
  border: 1px solid var(--vscode-border);
}

.terminal-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 18px;
  background: linear-gradient(180deg, #3c3c3c 0%, #2d2d2d 100%);
  border-bottom: 1px solid #1a1a1a;
}

.terminal-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  transition: var(--transition-fast);
}

.terminal-dot.red {
  background: radial-gradient(circle at 30% 30%, #ff8f86, #ff5f56);
  border: 1px solid #e0443e;
}

.terminal-dot.yellow {
  background: radial-gradient(circle at 30% 30%, #ffd466, #ffbd2e);
  border: 1px solid #dea123;
}

.terminal-dot.green {
  background: radial-gradient(circle at 30% 30%, #5edc79, #27c93f);
  border: 1px solid #1aab29;
}

.terminal-title {
  margin-left: 15px;
  font-size: 13px;
  color: var(--vscode-text-muted);
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

.terminal-body {
  padding: 25px;
  font-family: "Fira Code", "SF Mono", "Consolas", monospace;
  font-size: 14px;
  color: #4af626;
  line-height: 1.8;
  min-height: 250px;
  background: linear-gradient(180deg, #0c0c0c 0%, #050505 100%);
}

.terminal-body .command {
  color: #4af626;
}

.terminal-body .output {
  color: var(--vscode-text);
}

.terminal-body .success {
  color: var(--color-success);
}

.cursor {
  display: inline-block;
  width: 10px;
  height: 20px;
  background: #4af626;
  animation: cursorBlink 1s step-end infinite;
  vertical-align: middle;
  margin-left: 2px;
}

@keyframes cursorBlink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}

/* ============================================
     LEARNINGS LIST
     ============================================ */
.learnings-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.learnings-list li {
  padding: 16px 20px;
  background: linear-gradient(
    135deg,
    rgba(255, 183, 77, 0.1) 0%,
    transparent 100%
  );
  border-radius: 10px;
  border-left: 4px solid var(--color-warning);
  font-size: 14px;
  transition: var(--transition-fast);
  display: flex;
  align-items: center;
  gap: 12px;
}

.learnings-list li::before {
  content: "💡";
  font-size: 16px;
}

.learnings-list li:hover {
  transform: translateX(8px);
  background: linear-gradient(
    135deg,
    rgba(255, 183, 77, 0.15) 0%,
    transparent 100%
  );
}

/* ============================================
     FALLBACK MODAL (No Details)
     ============================================ */
.modal-fallback {
  background: linear-gradient(135deg, var(--vscode-bg) 0%, #1a1a1a 100%);
  padding: 50px;
  border-radius: 20px;
  text-align: center;
  max-width: 500px;
  color: var(--vscode-text-bright);
  box-shadow: var(--shadow-modal);
  border: 1px solid var(--vscode-border);
}

.modal-fallback h2 {
  margin-bottom: 20px;
  font-size: 24px;
}

.modal-fallback p {
  color: var(--vscode-text-muted);
  margin-bottom: 25px;
  line-height: 1.6;
}

.modal-fallback a {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: var(--color-info);
  text-decoration: none;
  margin-bottom: 25px;
  padding: 12px 24px;
  background: rgba(0, 122, 204, 0.1);
  border-radius: 8px;
  transition: var(--transition-fast);
}

.modal-fallback a:hover {
  background: var(--vscode-accent);
  color: white;
}

.modal-fallback button {
  padding: 12px 35px;
  background: linear-gradient(135deg, var(--vscode-accent) 0%, #0062a3 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: var(--transition-fast);
}

.modal-fallback button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 122, 204, 0.4);
}

/* ============================================
     ANIMATIONS
     ============================================ */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.editor-content > * {
  animation: fadeInUp 0.5s ease forwards;
}

.editor-content > *:nth-child(1) {
  animation-delay: 0.05s;
}
.editor-content > *:nth-child(2) {
  animation-delay: 0.1s;
}
.editor-content > *:nth-child(3) {
  animation-delay: 0.15s;
}
.editor-content > *:nth-child(4) {
  animation-delay: 0.2s;
}
.editor-content > *:nth-child(5) {
  animation-delay: 0.25s;
}
.editor-content > *:nth-child(6) {
  animation-delay: 0.3s;
}

/* ============================================
     RESPONSIVE - TABLET
     ============================================ */
@media (max-width: 1024px) {
  .vscode-modal {
    max-width: 100%;
    height: 95vh;
    margin: 10px;
    border-radius: 10px;
  }

  .vscode-sidebar {
    width: 220px;
  }

  .editor-content {
    padding: 25px 30px;
  }

  .editor-content h1 {
    font-size: 26px;
  }

  .tech-categories {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* ============================================
     RESPONSIVE - MOBILE
     ============================================ */
@media (max-width: 768px) {
  .modal-overlay {
    padding: 0;
    align-items: flex-end;
  }

  .vscode-modal {
    width: 100%;
    height: 95vh;
    max-height: none;
    border-radius: 20px 20px 0 0;
    animation: mobileSlideUp 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  }

  @keyframes mobileSlideUp {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }

  /* Hide activity bar & sidebar on mobile */
  .vscode-activitybar {
    display: none;
  }

  .vscode-sidebar {
    display: none;
  }

  /* Mobile Title Bar */
  .vscode-titlebar {
    height: 55px;
    padding: 0 12px;
  }

  .titlebar-left {
    font-size: 14px;
    gap: 8px;
  }

  .titlebar-left span {
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  /* Mobile Tabs - Horizontal Scroll */
  .editor-tabs {
    padding: 0 10px;
    gap: 5px;
  }

  .editor-tab {
    padding: 10px 12px;
    font-size: 12px;
    border-radius: 8px 8px 0 0;
    border-right: none;
    background: var(--vscode-sidebar);
    flex-shrink: 0;
  }

  .editor-tab.active {
    background: var(--vscode-bg);
  }

  .tab-close {
    display: none;
  }

  /* Mobile Breadcrumb */
  .editor-breadcrumb {
    padding: 8px 15px;
    font-size: 11px;
  }

  /* Mobile Content */
  .editor-content {
    padding: 20px 18px;
  }

  .editor-content h1 {
    font-size: 22px;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .editor-content h2 {
    font-size: 18px;
  }

  .editor-content h2::before {
    display: none;
  }

  .project-tagline {
    font-size: 15px;
    padding: 12px 15px;
  }

  /* Mobile Architecture Flow */
  .architecture-flow {
    flex-direction: column;
    padding: 20px;
    gap: 15px;
  }

  .flow-step {
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;
    padding: 18px 20px;
  }

  .flow-arrow {
    transform: rotate(90deg);
    font-size: 20px;
  }

  /* Mobile Grids */
  .tech-categories,
  .impact-grid,
  .screenshots-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  /* Mobile Code Block */
  .code-block pre {
    padding: 15px;
    font-size: 12px;
  }

  /* Mobile Terminal */
  .terminal-body {
    padding: 15px;
    font-size: 12px;
    min-height: 180px;
  }

  /* Mobile Status Bar */
  .vscode-statusbar {
    height: 32px;
    font-size: 11px;
  }

  .statusbar-left,
  .statusbar-right {
    gap: 10px;
  }

  .statusbar-right {
    display: none;
  }

  /* Mobile Table */
  .info-table {
    display: block;
  }

  .info-table tbody {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .info-table tr {
    display: flex;
    flex-direction: column;
    background: var(--vscode-sidebar);
    border-radius: 8px;
    overflow: hidden;
  }

  .info-table td {
    border: none;
    padding: 12px 15px;
  }

  .info-table td:first-child {
    width: 100%;
    border-bottom: 1px solid var(--vscode-border);
    font-size: 12px;
  }

  /* Mobile Badges */
  .badges {
    gap: 8px;
  }

  .badge {
    padding: 5px 12px;
    font-size: 11px;
  }

  /* Mobile Placeholders */
  .placeholder-screenshots,
  .placeholder-demo {
    padding: 50px 20px;
  }

  .placeholder-icon {
    font-size: 48px;
  }
}

/* ============================================
     RESPONSIVE - SMALL MOBILE
     ============================================ */
@media (max-width: 480px) {
  .vscode-modal {
    border-radius: 15px 15px 0 0;
  }

  .vscode-titlebar {
    height: 45px;
  }

  .editor-tabs {
    padding: 0 8px;
  }

  .editor-tab {
    padding: 8px 10px;
    font-size: 11px;
  }

  .editor-content {
    padding: 15px 12px;
  }

  .editor-content h1 {
    font-size: 20px;
  }

  .editor-content h2 {
    font-size: 16px;
  }

  .problem-box,
  .solution-box {
    padding: 18px;
  }

  .problem-box::before,
  .solution-box::before {
    display: none;
  }

  .flow-step {
    padding: 15px;
  }

  .step-number {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }

  .impact-card {
    padding: 15px;
    gap: 12px;
  }

  .tech-category {
    padding: 15px;
  }

  .tech-badge {
    padding: 5px 10px;
    font-size: 12px;
  }
}

/* ============================================
     DARK/LIGHT THEME SUPPORT
     ============================================ */
.light-theme .vscode-modal {
  --vscode-bg: #ffffff;
  --vscode-sidebar: #f3f3f3;
  --vscode-activitybar: #2c2c2c;
  --vscode-titlebar: #dddddd;
  --vscode-border: #e5e5e5;
  --vscode-text: #333333;
  --vscode-text-muted: #717171;
  --vscode-text-bright: #000000;
  --vscode-hover: #e8e8e8;
  --vscode-selection: #add6ff;
}

.light-theme .vscode-titlebar {
  background: linear-gradient(180deg, #f0f0f0 0%, var(--vscode-titlebar) 100%);
}

.light-theme .editor-tab {
  background: #ececec;
}

.light-theme .editor-tab.active {
  background: var(--vscode-bg);
}

.light-theme .code-block,
.light-theme .content-terminal {
  background: #1e1e1e;
}

.light-theme .terminal-body {
  background: linear-gradient(180deg, #1e1e1e 0%, #0c0c0c 100%);
}

/* ============================================
     ACCESSIBILITY
     ============================================ */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus Styles */
.editor-tab:focus,
.tree-item:focus,
.activity-icon:focus,
.titlebar-btn:focus {
  outline: 2px solid var(--vscode-accent);
  outline-offset: -2px;
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .vscode-modal {
    border: 2px solid white;
  }

  .editor-tab.active {
    border: 2px solid var(--vscode-accent);
  }

  .tree-item.active {
    border: 2px solid var(--vscode-accent);
  }
}

/* ============================================
     PRINT STYLES
     ============================================ */
@media print {
  .modal-overlay {
    position: static;
    background: white;
  }

  .vscode-modal {
    box-shadow: none;
    border: 1px solid #ccc;
    height: auto;
    max-height: none;
  }

  .vscode-activitybar,
  .vscode-sidebar,
  .vscode-titlebar,
  .vscode-statusbar,
  .editor-tabs {
    display: none;
  }

  .editor-content {
    padding: 20px;
    color: black;
  }
}

/* ============================================
   GLASSMORPHISM UTILITIES
   ============================================ */

.glassmorphism {
  background: rgba(30, 30, 30, 0.7);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.glassmorphism-light {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* ============================================
     GRADIENT ANIMATED BORDERS
     ============================================ */

.gradient-border {
  position: relative;
  background: var(--vscode-bg);
}

.gradient-border::before {
  content: "";
  position: absolute;
  inset: -2px;
  background: linear-gradient(
    45deg,
    #667eea,
    #764ba2,
    #f093fb,
    #f5576c,
    #667eea
  );
  background-size: 400% 400%;
  border-radius: inherit;
  z-index: -1;
  animation: gradientRotate 6s ease infinite;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.gradient-border:hover::before,
.gradient-border.active::before {
  opacity: 1;
}

@keyframes gradientRotate {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Tab with Gradient Border */
.editor-tab.active {
  position: relative;
}

.editor-tab.active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #667eea, #764ba2, #f093fb);
  background-size: 200% 100%;
  animation: tabGradient 3s ease infinite;
}

@keyframes tabGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ============================================
     GLASSMORPHISM TOOLTIPS FOR TECH BADGES
     ============================================ */

.tech-badge {
  position: relative;
  cursor: pointer;
}

.tech-badge::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%) scale(0.8);
  padding: 10px 16px;
  background: rgba(20, 20, 20, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  font-size: 12px;
  color: #fff;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  z-index: 100;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
}

.tech-badge::before {
  content: "";
  position: absolute;
  bottom: calc(100% + 4px);
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: rgba(20, 20, 20, 0.95);
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.tech-badge:hover::after,
.tech-badge:hover::before {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) scale(1);
}

/* ============================================
     MICRO-INTERACTIONS
     ============================================ */

/* Button Scale & Bounce */
.project-btn {
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.project-btn:hover {
  transform: translateY(-3px) scale(1.02);
}

.project-btn:active {
  transform: translateY(-1px) scale(0.98);
}

/* Icon Bounce */
.project-btn svg,
.activity-icon svg,
.titlebar-btn svg {
  transition: transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.project-btn:hover svg {
  transform: scale(1.15);
}

.activity-icon:hover svg {
  animation: iconBounce 0.5s ease;
}

@keyframes iconBounce {
  0%,
  100% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-4px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(-2px);
  }
}

/* Tree Item Hover Slide */
.tree-item {
  transition: all 0.2s ease;
}

.tree-item:hover {
  padding-left: 16px;
  background: var(--vscode-hover);
}

/* Badge Hover Pop */
.badge {
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.badge:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

/* Impact Card Hover */
.impact-card {
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.impact-card:hover {
  transform: translateX(10px) scale(1.02);
}

.impact-card:hover .impact-icon {
  animation: iconPop 0.4s ease;
}

@keyframes iconPop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

/* ============================================
     SMOOTH PAGE TRANSITIONS (TAB CONTENT)
     ============================================ */

.editor-content {
  animation: contentFadeIn 0.4s ease;
}

@keyframes contentFadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Staggered Children Animation */
.editor-content > * {
  opacity: 90;
  animation: staggerFadeIn 0.5s ease forwards;
}

.editor-content > *:nth-child(1) {
  animation-delay: 0.05s;
}
.editor-content > *:nth-child(2) {
  animation-delay: 0.1s;
}
.editor-content > *:nth-child(3) {
  animation-delay: 0.15s;
}
.editor-content > *:nth-child(4) {
  animation-delay: 0.2s;
}
.editor-content > *:nth-child(5) {
  animation-delay: 0.25s;
}
.editor-content > *:nth-child(6) {
  animation-delay: 0.3s;
}
.editor-content > *:nth-child(7) {
  animation-delay: 0.35s;
}
.editor-content > *:nth-child(8) {
  animation-delay: 0.4s;
}

@keyframes staggerFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ============================================
     STORYTELLING FORMAT - PROBLEM SOLVING
     ============================================ */

.story-section {
  margin: 30px 0;
  padding: 25px;
  border-radius: 16px;
  position: relative;
  overflow: hidden;
}

.story-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(180deg, var(--story-color), transparent);
}

.story-section.problem {
  --story-color: #ef4444;
  background: linear-gradient(
    135deg,
    rgba(239, 68, 68, 0.1) 0%,
    transparent 100%
  );
}

.story-section.approach {
  --story-color: #f59e0b;
  background: linear-gradient(
    135deg,
    rgba(245, 158, 11, 0.1) 0%,
    transparent 100%
  );
}

.story-section.solution {
  --story-color: #22c55e;
  background: linear-gradient(
    135deg,
    rgba(34, 197, 94, 0.1) 0%,
    transparent 100%
  );
}

.story-section.result {
  --story-color: #3b82f6;
  background: linear-gradient(
    135deg,
    rgba(59, 130, 246, 0.1) 0%,
    transparent 100%
  );
}

.story-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 15px;
}

.story-number {
  width: 32px;
  height: 32px;
  background: var(--story-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 14px;
  color: #fff;
}

.story-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--story-color);
  margin: 0;
}

.story-content {
  color: rgba(255, 255, 255, 0.8);
  font-size: 15px;
  line-height: 1.7;
  margin: 0;
}

/* Connector Line Between Story Sections */
.story-connector {
  width: 2px;
  height: 30px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2), transparent);
  margin: 0 auto;
}

/* ============================================
     FOCUS/ACTIVE STATES FOR ACCESSIBILITY
     ============================================ */

.editor-tab:focus-visible,
.tree-item:focus-visible,
.project-btn:focus-visible {
  outline: 2px solid var(--vscode-accent);
  outline-offset: 2px;
}

/* ============================================
     RESPONSIVE MICRO-INTERACTIONS
     ============================================ */

@media (max-width: 768px) {
  /* Reduce motion on mobile for performance */
  .project-btn:hover {
    transform: translateY(-2px);
  }

  .badge:hover {
    transform: scale(1.02);
  }

  .impact-card:hover {
    transform: translateX(5px);
  }

  /* Disable complex animations */
  .gradient-border::before {
    animation: none;
  }
}

/* Respect user motion preferences */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }

  .shimmer::after {
    animation: none;
  }
}

/* ✅ FIX: Stop nodes from jumping/bouncing on hover */
.arch-node:hover {
  transform: scale(
    1.05
  ) !important; /* Only zoom slightly, don't move up/down */
  animation: none !important; /* Stop any continuous pulse animations */
}

/* ============================================
   MEDIA MENTIONS / NEWS LINKS
   ============================================ */
.media-mentions {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.media-mentions h5 {
  color: var(--vscode-text-muted);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0 0 12px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.media-links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
}

.media-link-card {
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid var(--vscode-border);
  padding: 12px;
  border-radius: 8px;
  text-decoration: none;
  transition: all 0.2s ease;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.media-link-card:hover {
  background: var(--vscode-hover);
  border-color: var(--vscode-accent);
  transform: translateY(-2px);
}

.media-source {
  font-size: 11px;
  font-weight: 700;
  color: var(--vscode-accent);
  text-transform: uppercase;
}

.media-title {
  font-size: 13px;
  color: var(--vscode-text-bright);
  line-height: 1.4;
  padding-right: 15px; /* Space for icon */
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Limit to 2 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.media-icon {
  position: absolute;
  top: 12px;
  right: 12px;
  font-size: 10px;
  color: var(--vscode-text-muted);
  opacity: 0.5;
}

.media-link-card:hover .media-icon {
  opacity: 1;
  color: var(--vscode-accent);
}

/* ============================================
   NEW FEATURES - ENHANCED STYLES
   Append to existing ProjectModal.css
   ============================================ */

/* ============================================
   COMMAND PALETTE
   ============================================ */
.command-palette-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 15%;
  animation: overlayFadeIn 0.15s ease;
}

.command-palette {
  width: 90%;
  max-width: 620px;
  background: #252526;
  border-radius: 10px;
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.6),
    0 0 0 1px rgba(255, 255, 255, 0.08);
  overflow: hidden;
  animation: paletteSlideDown 0.2s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes paletteSlideDown {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.98);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.command-input-wrapper {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid var(--vscode-border);
  gap: 10px;
}

.command-search-icon {
  color: var(--vscode-text-muted);
  font-size: 14px;
  flex-shrink: 0;
}

.command-input {
  flex: 1;
  background: transparent;
  border: none;
  color: var(--vscode-text-bright);
  font-size: 15px;
  font-family: inherit;
  outline: none;
}

.command-input::placeholder {
  color: var(--vscode-text-muted);
}

.command-kbd {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid var(--vscode-border);
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 11px;
  color: var(--vscode-text-muted);
  font-family: inherit;
}

.command-list {
  max-height: 350px;
  overflow-y: auto;
  padding: 6px;
}

.command-list::-webkit-scrollbar {
  width: 6px;
}

.command-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.command-empty {
  padding: 20px;
  text-align: center;
  color: var(--vscode-text-muted);
  font-size: 13px;
}

.command-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.1s;
}

.command-item.selected,
.command-item:hover {
  background: #04395e;
}

.command-item-icon {
  color: var(--vscode-accent);
  font-size: 14px;
  width: 20px;
  text-align: center;
  flex-shrink: 0;
}

.command-item-text {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.command-item-label {
  color: var(--vscode-text-bright);
  font-size: 14px;
}

.command-item-desc {
  color: var(--vscode-text-muted);
  font-size: 12px;
  margin-top: 2px;
}

.command-shortcut {
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid var(--vscode-border);
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 11px;
  color: var(--vscode-text-muted);
  font-family: inherit;
  flex-shrink: 0;
}

.command-item-category {
  font-size: 11px;
  color: var(--vscode-text-muted);
  background: rgba(255, 255, 255, 0.05);
  padding: 2px 8px;
  border-radius: 4px;
  flex-shrink: 0;
}

/* ============================================
     COMMAND PALETTE TRIGGER (Title Bar)
     ============================================ */
.titlebar-center {
  flex: 1;
  display: flex;
  justify-content: center;
  max-width: 400px;
}

.command-palette-trigger {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 14px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: var(--vscode-text-muted);
  font-size: 12px;
  cursor: pointer;
  width: 100%;
  max-width: 300px;
  transition: all 0.2s;
}

.command-palette-trigger:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.15);
  color: var(--vscode-text);
}

.command-palette-trigger kbd {
  margin-left: auto;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 1px 6px;
  border-radius: 3px;
  font-size: 10px;
  font-family: inherit;
}

.command-palette-trigger svg {
  font-size: 11px;
}

/* ============================================
     NOTIFICATION TOASTS
     ============================================ */
.toast-container {
  position: absolute;
  bottom: 50px;
  right: 20px;
  z-index: 500;
  display: flex;
  flex-direction: column-reverse;
  gap: 8px;
  pointer-events: none;
}

.toast {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 16px;
  background: #252526;
  border: 1px solid var(--vscode-border);
  border-radius: 8px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.4);
  min-width: 250px;
  pointer-events: all;
  animation: toastSlideIn 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes toastSlideIn {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.toast-icon {
  font-size: 16px;
  flex-shrink: 0;
}

.toast-info .toast-icon {
  color: var(--color-info);
}
.toast-success .toast-icon {
  color: var(--color-success);
}
.toast-warning .toast-icon {
  color: var(--color-warning);
}
.toast-error .toast-icon {
  color: var(--color-error);
}

.toast-message {
  flex: 1;
  color: var(--vscode-text);
  font-size: 13px;
}

.toast-close {
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  transition: 0.15s;
}

.toast-close:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--vscode-text);
}

/* ============================================
     TAB CONTEXT MENU
     ============================================ */
.tab-context-menu {
  position: fixed;
  background: #2d2d2d;
  border: 1px solid var(--vscode-border);
  border-radius: 8px;
  padding: 6px;
  z-index: 1000;
  min-width: 180px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  animation: contextFadeIn 0.15s ease;
}

@keyframes contextFadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.context-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 7px 12px;
  color: var(--vscode-text);
  font-size: 13px;
  cursor: pointer;
  border-radius: 4px;
  transition: background 0.1s;
}

.context-item:hover {
  background: #04395e;
}

.context-item svg {
  font-size: 12px;
  color: var(--vscode-text-muted);
}

.context-separator {
  height: 1px;
  background: var(--vscode-border);
  margin: 4px 8px;
}

/* ============================================
     SCROLL PROGRESS BAR
     ============================================ */
.scroll-progress {
  height: 2px;
  background: linear-gradient(90deg, #667eea, #764ba2, #f093fb);
  transition: width 0.1s linear;
  flex-shrink: 0;
  position: relative;
  z-index: 10;
}

/* ============================================
     MINIMAP
     ============================================ */
.minimap {
  width: 65px;
  flex-shrink: 0;
  background: rgba(30, 30, 30, 0.6);
  border-left: 1px solid var(--vscode-border);
  position: relative;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
}

.minimap-content {
  padding: 10px 8px;
}

.minimap-line {
  background: rgba(204, 204, 204, 0.3);
  border-radius: 1px;
  margin-bottom: 1px;
}

.minimap-viewport {
  position: absolute;
  left: 0;
  right: 0;
  background: rgba(0, 122, 204, 0.15);
  border-left: 2px solid rgba(0, 122, 204, 0.6);
  transition: top 0.1s linear;
  pointer-events: none;
  min-height: 10px;
}

.minimap:hover .minimap-viewport {
  background: rgba(0, 122, 204, 0.25);
}

/* ============================================
     EDITOR BODY (Editor + Minimap flex)
     ============================================ */
.editor-body {
  flex: 1;
  display: flex;
  overflow: hidden;
  min-height: 0;
}

.editor-body .editor-content {
  flex: 1;
  min-width: 0;
}

/* ============================================
     RESIZABLE SIDEBAR
     ============================================ */
.resize-handle {
  width: 4px;
  cursor: col-resize;
  background: transparent;
  transition: background 0.2s;
  flex-shrink: 0;
  position: relative;
  z-index: 10;
}

.resize-handle:hover,
.resize-handle.active {
  background: var(--vscode-accent);
}

.resize-handle::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: -4px;
  right: -4px;
}

/* ============================================
     BOTTOM PANEL (Terminal)
     ============================================ */
.bottom-panel {
  border-top: 1px solid var(--vscode-border);
  display: flex;
  flex-direction: column;
  max-height: 280px;
  min-height: 150px;
  flex-shrink: 0;
  animation: panelSlideUp 0.2s ease;
}

@keyframes panelSlideUp {
  from {
    max-height: 0;
    opacity: 0;
  }
  to {
    max-height: 280px;
    opacity: 1;
  }
}

.bottom-panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
  background: var(--vscode-sidebar);
  border-bottom: 1px solid var(--vscode-border);
  height: 36px;
  flex-shrink: 0;
}

.bottom-panel-tabs {
  display: flex;
  gap: 4px;
}

.bottom-panel-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  font-size: 12px;
  color: var(--vscode-text-muted);
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.bottom-panel-tab.active {
  color: var(--vscode-text-bright);
  border-bottom-color: var(--vscode-accent);
}

.bottom-panel-tab:hover {
  color: var(--vscode-text);
}

.problems-count {
  background: var(--vscode-accent);
  color: white;
  padding: 0 6px;
  border-radius: 8px;
  font-size: 10px;
  min-width: 16px;
  text-align: center;
}

.bottom-panel-actions {
  display: flex;
  gap: 4px;
}

.panel-close-btn {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  border-radius: 4px;
  transition: 0.15s;
}

.panel-close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--vscode-text);
}

.bottom-panel-body {
  flex: 1;
  overflow-y: auto;
  background: #0c0c0c;
}

.bottom-panel-body .terminal-body {
  min-height: unset;
  padding: 12px 18px;
  font-size: 13px;
}

/* ============================================
     STAT CARDS (Animated Counters)
     ============================================ */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 16px;
  margin: 20px 0 30px;
}

.stat-card {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.04) 0%,
    rgba(255, 255, 255, 0.01) 100%
  );
  border: 1px solid var(--vscode-border);
  border-radius: 14px;
  padding: 22px 18px;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--stat-color, var(--vscode-accent));
  opacity: 0.8;
}

.stat-card:hover {
  transform: translateY(-5px);
  border-color: var(--stat-color, var(--vscode-accent));
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
}

.stat-icon {
  font-size: 28px;
  margin-bottom: 10px;
}

.stat-value {
  font-size: 36px;
  font-weight: 800;
  color: var(--stat-color, var(--vscode-accent));
  font-family: "JetBrains Mono", "Fira Code", monospace;
  line-height: 1;
  margin-bottom: 6px;
}

.stat-label {
  font-size: 12px;
  color: var(--vscode-text-muted);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.stat-bar {
  margin-top: 14px;
  height: 4px;
  background: rgba(255, 255, 255, 0.06);
  border-radius: 2px;
  overflow: hidden;
}

.stat-bar-fill {
  height: 100%;
  background: var(--stat-color, var(--vscode-accent));
  border-radius: 2px;
  transition: width 1.8s cubic-bezier(0.16, 1, 0.3, 1);
}

/* ============================================
     PROFICIENCY BARS
     ============================================ */
.proficiency-list {
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.proficiency-item {
  width: 100%;
}

.proficiency-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.proficiency-name {
  font-size: 13px;
  color: var(--vscode-text);
  font-weight: 500;
}

.proficiency-level {
  font-size: 12px;
  color: var(--vscode-text-muted);
  font-family: "Fira Code", monospace;
}

.proficiency-track {
  height: 6px;
  background: rgba(255, 255, 255, 0.06);
  border-radius: 3px;
  overflow: hidden;
}

.proficiency-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 1.5s cubic-bezier(0.16, 1, 0.3, 1);
  position: relative;
}

.proficiency-fill::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 20px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3));
  border-radius: 0 3px 3px 0;
}

/* ============================================
     ENHANCED CODE BLOCK
     ============================================ */
.code-block.enhanced {
  border: none;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.05);
}

.code-dots {
  display: flex;
  gap: 8px;
  margin-right: 12px;
}

.code-dots .dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.code-dots .dot.red {
  background: #ff5f56;
}
.code-dots .dot.yellow {
  background: #ffbd2e;
}
.code-dots .dot.green {
  background: #27c93f;
}

.code-body-wrapper {
  display: flex;
}

.code-line-numbers {
  display: flex;
  flex-direction: column;
  padding: 20px 12px 20px 18px;
  background: rgba(0, 0, 0, 0.2);
  border-right: 1px solid var(--vscode-border);
  user-select: none;
  min-width: 45px;
  text-align: right;
}

.code-line-numbers span {
  font-family: "Fira Code", monospace;
  font-size: 13px;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.2);
}

/* ============================================
     LINE NUMBERS GUTTER
     ============================================ */
.line-numbers-gutter {
  position: absolute;
  left: 0;
  top: 0;
  width: 50px;
  padding: 30px 8px 30px 0;
  text-align: right;
  pointer-events: none;
  z-index: 1;
}

.line-number {
  font-family: "Fira Code", monospace;
  font-size: 13px;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.15);
  height: 21px;
}

/* ============================================
     SEARCH PANEL (Sidebar)
     ============================================ */
.search-panel {
  padding: 12px;
}

.search-input-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid var(--vscode-border);
  border-radius: 6px;
  transition: border-color 0.2s;
}

.search-input-wrapper:focus-within {
  border-color: var(--vscode-accent);
}

.search-icon {
  color: var(--vscode-text-muted);
  font-size: 12px;
  flex-shrink: 0;
}

.search-input {
  flex: 1;
  background: transparent;
  border: none;
  color: var(--vscode-text);
  font-size: 13px;
  outline: none;
  font-family: inherit;
}

.search-input::placeholder {
  color: var(--vscode-text-muted);
}

.search-clear {
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  padding: 2px;
  display: flex;
  border-radius: 3px;
}

.search-clear:hover {
  color: var(--vscode-text);
  background: rgba(255, 255, 255, 0.1);
}

.search-results {
  margin-top: 12px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.search-result-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  color: var(--vscode-text);
  font-size: 13px;
  cursor: pointer;
  border-radius: 4px;
  transition: 0.15s;
}

.search-result-item:hover {
  background: var(--vscode-hover);
}

.search-result-item svg {
  color: var(--vscode-text-muted);
  font-size: 12px;
}

/* ============================================
     SIDEBAR KEYBOARD SHORTCUTS
     ============================================ */
.sidebar-shortcuts {
  padding: 12px 16px;
  border-top: 1px solid var(--vscode-border);
  margin-top: auto;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.shortcut-hint {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 11px;
  color: var(--vscode-text-muted);
}

.shortcut-hint kbd {
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid var(--vscode-border);
  padding: 1px 6px;
  border-radius: 3px;
  font-size: 10px;
  font-family: inherit;
  min-width: 28px;
  text-align: center;
}

/* ============================================
     ACTIVITY BAR ENHANCEMENTS
     ============================================ */
.activity-spacer {
  flex: 1;
}

.activity-badge {
  position: absolute;
  top: 6px;
  right: 6px;
  min-width: 16px;
  height: 16px;
  background: var(--vscode-accent);
  color: white;
  font-size: 10px;
  font-weight: 700;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 4px;
  line-height: 1;
}

/* ============================================
     PINNED TAB STYLES
     ============================================ */
.editor-tab.pinned {
  padding-left: 10px;
  padding-right: 10px;
}

.editor-tab.pinned .tab-text {
  display: none;
}

.pin-icon {
  font-size: 10px;
  color: var(--vscode-accent);
  transform: rotate(45deg);
}

/* ============================================
     TAB ACTIONS
     ============================================ */
.tab-actions {
  margin-left: auto;
  display: flex;
  align-items: center;
  padding: 0 8px;
}

.tab-action-btn {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  cursor: pointer;
  border-radius: 4px;
  transition: 0.15s;
}

.tab-action-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--vscode-text);
}

/* ============================================
     WELCOME TAB
     ============================================ */
.welcome-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 400px;
}

.welcome-content {
  text-align: center;
  max-width: 400px;
}

.welcome-icon {
  font-size: 64px;
  color: var(--vscode-text-muted);
  opacity: 0.3;
  margin-bottom: 20px;
}

.welcome-content h2 {
  color: var(--vscode-text);
  font-size: 24px;
  margin-bottom: 10px;
}

.welcome-content h2::before {
  display: none;
}

.welcome-content p {
  color: var(--vscode-text-muted);
  font-size: 14px;
  margin-bottom: 30px;
}

.welcome-shortcuts {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.shortcut-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  color: var(--vscode-text-muted);
}

.shortcut-item kbd {
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid var(--vscode-border);
  padding: 4px 10px;
  border-radius: 4px;
  font-size: 12px;
  font-family: inherit;
  color: var(--vscode-text);
  min-width: 40px;
  text-align: center;
}

/* ============================================
     ZEN MODE
     ============================================ */
.vscode-modal.zen-mode .vscode-activitybar,
.vscode-modal.zen-mode .vscode-sidebar,
.vscode-modal.zen-mode .resize-handle,
.vscode-modal.zen-mode .vscode-statusbar,
.vscode-modal.zen-mode .editor-tabs,
.vscode-modal.zen-mode .editor-breadcrumb {
  display: none;
}

.vscode-modal.zen-mode .vscode-titlebar {
  opacity: 0;
  transition: opacity 0.3s;
}

.vscode-modal.zen-mode:hover .vscode-titlebar {
  opacity: 1;
}

.vscode-modal.zen-mode .editor-content {
  padding: 60px 80px;
  max-width: 900px;
  margin: 0 auto;
}

/* ============================================
     BREADCRUMB ACTIVE STATE
     ============================================ */
.breadcrumb-item.active {
  color: var(--vscode-text);
  font-weight: 500;
}

/* ============================================
     MOBILE BOTTOM TAB BAR
     ============================================ */
.mobile-tab-bar {
  display: none;
  background: var(--vscode-sidebar);
  border-top: 1px solid var(--vscode-border);
  padding: 6px 8px;
  padding-bottom: max(6px, env(safe-area-inset-bottom));
  gap: 4px;
  flex-shrink: 0;
}

.mobile-tab {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3px;
  padding: 6px 4px;
  background: transparent;
  border: none;
  color: var(--vscode-text-muted);
  font-size: 10px;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.2s;
}

.mobile-tab svg {
  font-size: 18px;
}

.mobile-tab.active {
  color: var(--vscode-accent);
  background: rgba(0, 122, 204, 0.1);
}

.mobile-tab span {
  font-weight: 600;
  letter-spacing: 0.3px;
}

/* ============================================
     MOBILE RESPONSIVE - ENHANCED
     ============================================ */
@media (max-width: 768px) {
  .mobile-tab-bar {
    display: flex;
  }

  .editor-tabs {
    display: none;
  }

  .command-palette-trigger {
    display: none;
  }

  .titlebar-center {
    display: none;
  }

  .minimap {
    display: none;
  }

  .bottom-panel {
    max-height: 200px;
  }

  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .stat-value {
    font-size: 26px;
  }

  .stat-label {
    font-size: 10px;
  }

  .stat-card {
    padding: 16px 12px;
  }

  .toast-container {
    left: 15px;
    right: 15px;
    bottom: 80px;
  }

  .toast {
    min-width: unset;
  }

  .command-palette {
    width: 95%;
    margin-top: 10%;
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr 1fr;
    gap: 8px;
  }

  .stat-card {
    padding: 14px 10px;
  }

  .stat-value {
    font-size: 22px;
  }

  .vscode-modal.zen-mode .editor-content {
    padding: 30px 20px;
  }
}

/* ============================================
     LIGHT THEME ADDITIONS
     ============================================ */
.light-theme .command-palette {
  background: #f3f3f3;
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.3);
}

.light-theme .command-item.selected {
  background: #0060c0;
  color: white;
}

.light-theme .command-item.selected .command-item-label,
.light-theme .command-item.selected .command-item-desc {
  color: white;
}

.light-theme .toast {
  background: #ffffff;
  border-color: #e0e0e0;
}

.light-theme .tab-context-menu {
  background: #f3f3f3;
  border-color: #e0e0e0;
}

.light-theme .stat-card {
  background: rgba(0, 0, 0, 0.02);
  border-color: #e0e0e0;
}

.light-theme .bottom-panel-body {
  background: #1e1e1e;
}

/* ============================================
     LOADING TRANSITION BETWEEN TABS
     ============================================ */
.editor-content {
  position: relative;
}

.editor-content > div {
  animation: tabContentIn 0.35s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes tabContentIn {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ============================================
     SMOOTH HOVER STATES FOR ALL INTERACTIVE ELEMENTS  
     ============================================ */
.editor-tab {
  transition: all 0.15s ease;
}

.tree-item {
  transition: all 0.15s ease;
}

.activity-icon {
  transition: all 0.2s ease;
}

/* Prevent layout shift on hover */
.tree-item:hover {
  padding-left: 14px; /* was 16px — reduced to prevent jump */
}

/* ============================================
     PRINT STYLES ADDITIONS
     ============================================ */
@media print {
  .minimap,
  .bottom-panel,
  .mobile-tab-bar,
  .toast-container,
  .command-palette-overlay,
  .tab-context-menu,
  .scroll-progress {
    display: none !important;
  }
}

/* ============================================
   JARVIS INSIDE MODAL
   ============================================ */
.modal-jarvis-btn {
  position: absolute;
  /* Kept elevated position */
  bottom: 70px;
  right: 20px;
  z-index: 100;

  display: flex;
  align-items: center;
  gap: 8px; /* Increased gap slightly */

  /* INCREASED SIZE: Larger padding and font */
  padding: 10px 24px;
  font-size: 15px;

  border-radius: 30px; /* More rounded for larger size */
  border: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  font-weight: 700; /* Bold text */
  color: white;
  white-space: nowrap;

  /* Glass + gradient */
  background: linear-gradient(
    135deg,
    rgba(102, 126, 234, 0.6) 0%,
    rgba(118, 75, 162, 0.55) 100%
  );
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);

  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3),
    inset 0 1px 2px rgba(255, 255, 255, 0.2);

  transition: all 0.3s ease;
}

/* AUTOMATICALLY ADD "AI" TEXT */
.modal-jarvis-btn::after {
  content: "AI";
  font-weight: 800;
  letter-spacing: 0.5px;
}

.modal-jarvis-btn:hover {
  transform: translateY(-3px) scale(1.05); /* Enhanced hover effect */
  box-shadow: 0 10px 28px rgba(102, 126, 234, 0.5),
    inset 0 1px 3px rgba(255, 255, 255, 0.3);
}

.modal-jarvis-btn:active {
  transform: scale(0.97);
}

/* JARVIS Panel — slides up from bottom */
.modal-jarvis-panel {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60%;
  max-height: 500px;
  z-index: 200;
  border-top: 1px solid var(--vscode-border);
  border-radius: 12px 12px 0 0;
  overflow: hidden;
  animation: jarvisPanelUp 0.35s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes jarvisPanelUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .modal-jarvis-btn {
    bottom: 90px;
    right: 12px;
    /* Increased Mobile Size */
    padding: 8px 18px;
    font-size: 13px;
  }

  /* Hide original text span if it exists, but keep ::after ("AI") visible */
  .modal-jarvis-btn span {
    display: none;
  }

  /* Ensure "AI" text is visible on mobile */
  .modal-jarvis-btn::after {
    display: block;
  }

  .modal-jarvis-panel {
    height: 70%;
  }
}




/* ============================================
   JARVIS AUTO-PROMPT — Glass + Skeuomorphic
   ============================================ */
   .jarvis-prompt {
    position: absolute;
    bottom: 50px;
    right: 20px;
    z-index: 150;
  
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 18px;
    max-width: 300px;
  
    /* ✅ Glassmorphism base */
    background: linear-gradient(
      135deg,
      rgba(102, 126, 234, 0.12) 0%,
      rgba(118, 75, 162, 0.08) 100%
    );
    backdrop-filter: blur(25px) saturate(180%);
    -webkit-backdrop-filter: blur(25px) saturate(180%);
  
    /* ✅ Skeuomorphic depth — light top, dark bottom */
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-top-color: rgba(255, 255, 255, 0.3);
    border-bottom-color: rgba(0, 0, 0, 0.1);
    border-radius: 16px;
  
    box-shadow:
      /* Outer glow */
      0 8px 32px rgba(102, 126, 234, 0.2),
      0 4px 16px rgba(0, 0, 0, 0.15),
      /* Inner highlight — top */
      inset 0 1px 2px rgba(255, 255, 255, 0.15),
      /* Inner shadow — bottom */
      inset 0 -1px 2px rgba(0, 0, 0, 0.08);
  
    /* Entrance */
    animation: jarvisPromptIn 0.5s cubic-bezier(0.16, 1, 0.3, 1);
  }
  
  @keyframes jarvisPromptIn {
    from {
      opacity: 0;
      transform: translateY(15px) scale(0.9);
      filter: blur(4px);
    }
    to {
      opacity: 1;
      transform: translateY(0) scale(1);
      filter: blur(0);
    }
  }
  
  /* ✅ Shimmer sweep effect */
  .jarvis-prompt::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 60%;
    height: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.08),
      transparent
    );
    border-radius: inherit;
    animation: promptShimmer 3s ease-in-out infinite 1s;
    pointer-events: none;
  }
  
  @keyframes promptShimmer {
    0% { left: -100%; }
    100% { left: 200%; }
  }
  
  /* ✅ Subtle pulsing border glow */
  .jarvis-prompt::after {
    content: "";
    position: absolute;
    inset: -2px;
    border-radius: 18px;
    background: linear-gradient(
      135deg,
      rgba(102, 126, 234, 0.3),
      rgba(118, 75, 162, 0.2),
      rgba(102, 126, 234, 0.3)
    );
    z-index: -1;
    opacity: 0;
    animation: borderGlow 3s ease-in-out infinite;
  }
  
  @keyframes borderGlow {
    0%, 100% { opacity: 0; }
    50% { opacity: 0.6; }
  }
  
  /* Emoji icon */
  .jarvis-prompt > span:first-child {
    font-size: 26px;
    flex-shrink: 0;
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
    animation: promptIconBounce 2s ease-in-out infinite;
  }
  
  @keyframes promptIconBounce {
    0%, 100% { transform: translateY(0) scale(1); }
    50% { transform: translateY(-3px) scale(1.05); }
  }
  
  /* Text */
  .jarvis-prompt p {
    margin: 0;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.45;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    font-weight: 500;
  }
  
  .jarvis-prompt p strong {
    color: #a78bfa;
    font-weight: 700;
  }
  
  /* Action buttons container */
  .jarvis-prompt-actions {
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex-shrink: 0;
  }
  
  /* Primary CTA button */
  .jarvis-prompt-actions button {
    padding: 7px 14px;
    border: none;
    border-radius: 10px;
    font-size: 11px;
    font-weight: 700;
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.25s ease;
    position: relative;
    overflow: hidden;
  
    /* Glass gradient button */
    background: linear-gradient(
      135deg,
      rgba(102, 126, 234, 0.8) 0%,
      rgba(118, 75, 162, 0.75) 100%
    );
    color: white;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  
    /* Skeuomorphic depth */
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-top-color: rgba(255, 255, 255, 0.35);
    border-bottom-color: rgba(0, 0, 0, 0.1);
    box-shadow:
      0 3px 10px rgba(102, 126, 234, 0.3),
      inset 0 1px 1px rgba(255, 255, 255, 0.2);
  }
  
  .jarvis-prompt-actions button:hover {
    transform: translateY(-2px);
    box-shadow:
      0 6px 18px rgba(102, 126, 234, 0.45),
      inset 0 1px 2px rgba(255, 255, 255, 0.25);
  }
  
  .jarvis-prompt-actions button:active {
    transform: translateY(0) scale(0.97);
    box-shadow:
      0 2px 6px rgba(102, 126, 234, 0.25),
      inset 0 2px 3px rgba(0, 0, 0, 0.15);
  }
  
  /* Dismiss / close button */
  .jarvis-prompt-actions button.dismiss {
    background: rgba(255, 255, 255, 0.06);
    color: rgba(255, 255, 255, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.08);
    box-shadow: none;
    padding: 5px 10px;
    font-size: 13px;
  }
  
  .jarvis-prompt-actions button.dismiss:hover {
    background: rgba(255, 255, 255, 0.12);
    color: rgba(255, 255, 255, 0.8);
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* ============================================
     MOBILE
     ============================================ */
  @media (max-width: 768px) {
    .jarvis-prompt {
      right: 10px;
      left: 10px;
      bottom: 38px;
      max-width: none;
      padding: 10px 14px;
      gap: 10px;
      border-radius: 14px;
    }
  
    .jarvis-prompt > span:first-child {
      font-size: 22px;
    }
  
    .jarvis-prompt p {
      font-size: 12px;
    }
  
    .jarvis-prompt-actions {
      flex-direction: row;
    }
  
    .jarvis-prompt-actions button {
      padding: 6px 12px;
      font-size: 10px;
    }
  }
  
  /* ============================================
     REDUCED MOTION
     ============================================ */
  @media (prefers-reduced-motion: reduce) {
    .jarvis-prompt {
      animation: fadeIn 0.2s ease;
    }
  
    .jarvis-prompt::before,
    .jarvis-prompt::after {
      animation: none;
    }
  
    .jarvis-prompt > span:first-child {
      animation: none;
    }
  }
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }