/* ============================================
   RAG ARCHITECTURE DIAGRAM - CLEAN VERSION
   ============================================ */

.rag-architecture-diagram {
  background: #1e1e1e; /* Solid VSCode background */
  border-radius: 16px;
  padding: 20px;
  margin: 20px 0;
  border: 1px solid #3e3e42; /* VSCode border color */
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
}

/* Optional: Add VSCode-style subtle noise texture */
.rag-architecture-diagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(
      circle at 20% 50%,
      rgba(96, 139, 193, 0.03) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(206, 145, 120, 0.02) 0%,
      transparent 50%
    );
  pointer-events: none;
}

/* Header */
.rag-diagram-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 15px;
  position: relative;
  z-index: 5;
}

.rag-header-left h3 {
  margin: 0 0 8px 0;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
}

.rag-legend {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.rag-legend-item {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 10px;
  color: rgba(255, 255, 255, 0.7);
}

.rag-legend-item .dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.dot.main {
  background: #667eea;
}
.dot.rag {
  background: #f472b6;
}
.dot.data {
  background: #4ade80;
}
.dot.infra {
  background: #fbbf24;
}

/* Header Controls */
.rag-header-controls {
  display: flex;
  gap: 8px;
  align-items: center;
}

.rag-view-toggle {
  display: flex;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 6px;
  padding: 2px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.rag-view-btn {
  padding: 5px 10px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  font-size: 10px;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.2s;
}

.rag-view-btn:hover {
  color: rgba(255, 255, 255, 0.8);
}

.rag-view-btn.active {
  background: rgba(244, 114, 182, 0.3);
  color: #fff;
}

.rag-animate-btn {
  padding: 5px 12px;
  background: rgba(244, 114, 182, 0.2);
  border: 1px solid rgba(244, 114, 182, 0.4);
  border-radius: 15px;
  color: #f9a8d4;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.2s;
}

.rag-animate-btn.active {
  background: rgba(74, 222, 128, 0.2);
  border-color: rgba(74, 222, 128, 0.4);
  color: #4ade80;
}

/* Pipeline Steps */
.rag-pipeline-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 10px 15px;
  background: rgba(244, 114, 182, 0.08);
  border-radius: 10px;
  margin-bottom: 15px;
  border: 1px solid rgba(244, 114, 182, 0.15);
  flex-wrap: wrap;
}

.rag-pipeline-step {
  display: flex;
  align-items: center;
  gap: 4px;
}

.step-num {
  width: 18px;
  height: 18px;
  background: linear-gradient(135deg, #ec4899, #f472b6);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 9px;
  font-weight: 700;
  color: white;
}

.step-label {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.step-arrow {
  color: rgba(244, 114, 182, 0.4);
  font-size: 12px;
}

/* SVG Diagram */
.rag-diagram-svg {
  width: 100%;
  height: 280px;
  display: block;
}

/* Connection Animation */
.rag-connection.animating line {
  stroke-dasharray: 2 1;
  animation: ragDashMove 1s linear infinite;
}

@keyframes ragDashMove {
  to {
    stroke-dashoffset: -3;
  }
}

/* Node Styles */
.rag-node {
  transition: filter 0.2s ease;
}

.rag-node:hover {
  filter: brightness(1.2);
}

/* Tooltip */
.rag-tooltip {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  padding: 12px 16px;
  background: rgba(20, 20, 30, 0.95);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  min-width: 220px;
  max-width: 300px;
  z-index: 20;
  pointer-events: none;
  animation: ragTooltipIn 0.2s ease;
}

.tooltip-main {
  border-left: 3px solid #667eea;
}
.tooltip-rag {
  border-left: 3px solid #f472b6;
}
.tooltip-data {
  border-left: 3px solid #4ade80;
}
.tooltip-infra {
  border-left: 3px solid #fbbf24;
}

@keyframes ragTooltipIn {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.rag-tooltip-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.rag-tooltip-icon {
  font-size: 20px;
}

.rag-tooltip-title {
  font-weight: 600;
  color: #fff;
  font-size: 13px;
}

.rag-tooltip-badge {
  font-size: 8px;
  padding: 2px 6px;
  border-radius: 3px;
  font-weight: 600;
}

.rag-tooltip-badge.main {
  background: rgba(102, 126, 234, 0.3);
  color: #a5b4fc;
}
.rag-tooltip-badge.rag {
  background: rgba(244, 114, 182, 0.3);
  color: #f9a8d4;
}
.rag-tooltip-badge.data {
  background: rgba(74, 222, 128, 0.3);
  color: #86efac;
}
.rag-tooltip-badge.infra {
  background: rgba(251, 191, 36, 0.3);
  color: #fcd34d;
}

.rag-tooltip-desc {
  margin: 0;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.4;
}

/* Info Box */
.rag-info-box {
  position: absolute;
  bottom: 15px;
  right: 15px;
  background: rgba(244, 114, 182, 0.1);
  border: 1px solid rgba(244, 114, 182, 0.2);
  border-radius: 10px;
  padding: 12px;
  max-width: 180px;
  z-index: 10;
}

.rag-info-title {
  font-size: 11px;
  font-weight: 600;
  color: #f472b6;
  margin-bottom: 8px;
}

.rag-info-box ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.rag-info-box li {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.7);
  padding: 3px 0;
}

/* ============================================
     RESPONSIVE
     ============================================ */

@media (max-width: 768px) {
  .rag-architecture-diagram {
    padding: 15px;
  }

  .rag-diagram-header {
    flex-direction: column;
  }

  .rag-header-controls {
    width: 100%;
    justify-content: space-between;
  }

  .rag-diagram-svg {
    height: 220px;
  }

  .step-label {
    display: none;
  }

  .rag-tooltip {
    position: relative;
    bottom: auto;
    left: auto;
    transform: none;
    margin-top: 10px;
  }

  .rag-info-box {
    position: relative;
    right: auto;
    bottom: auto;
    max-width: 100%;
    margin-top: 10px;
  }
}

@media (min-width: 1024px) {
  .rag-diagram-svg {
    height: 320px;
  }
}

@media (min-width: 1200px) {
  .rag-diagram-svg {
    height: 360px;
  }
}

/* ============================================
   DEMO FEATURE STYLES
   ============================================ */

/* Demo Play Button */
.demo-play-btn {
  padding: 8px 16px;
  background: linear-gradient(135deg, #22c55e, #16a34a);
  border: none;
  border-radius: 20px;
  color: white;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(34, 197, 94, 0.3);
}

.demo-play-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(34, 197, 94, 0.4);
}

.demo-reset-btn {
  padding: 8px 16px;
  background: rgba(239, 68, 68, 0.2);
  border: 1px solid rgba(239, 68, 68, 0.4);
  border-radius: 20px;
  color: #fca5a5;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s;
}

.demo-reset-btn:hover {
  background: rgba(239, 68, 68, 0.3);
}

/* Query Input */
.demo-query-input {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  margin-bottom: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.query-label {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  white-space: nowrap;
}

.demo-query-input input {
  flex: 1;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 13px;
  outline: none;
}

.demo-query-input input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

/* Progress Bar */
.demo-progress {
  margin-bottom: 15px;
}

.demo-progress-bar {
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
  margin-bottom: 8px;
}

.demo-progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #22c55e, #facc15);
  border-radius: 2px;
  transition: width 0.5s ease;
}

.demo-progress-steps {
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
}

.demo-step-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.demo-step-dot.active {
  background: #facc15;
  box-shadow: 0 0 10px rgba(250, 204, 21, 0.5);
  transform: scale(1.3);
}

.demo-step-dot.done {
  background: #22c55e;
}

/* Output Panel */
.demo-output-panel {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(250, 204, 21, 0.3);
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 15px;
  animation: panelSlideIn 0.3s ease;
}

@keyframes panelSlideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.demo-output-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.demo-step-badge {
  background: linear-gradient(135deg, #facc15, #eab308);
  color: #000;
  padding: 3px 8px;
  border-radius: 10px;
  font-size: 10px;
  font-weight: 700;
}

.demo-step-title {
  color: #facc15;
  font-weight: 600;
  font-size: 14px;
}

.demo-output-content {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 8px;
}

.demo-output-label {
  font-size: 9px;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.demo-output-code {
  font-family: "Fira Code", monospace;
  font-size: 11px;
  color: #4ade80;
  word-break: break-all;
  display: block;
}

.demo-output-detail {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
}

/* Active Pipeline Step */
.rag-pipeline-step.active {
  background: rgba(250, 204, 21, 0.2);
  border: 1px solid rgba(250, 204, 21, 0.4);
  border-radius: 15px;
  padding: 4px 10px;
}

.rag-pipeline-step.active .step-num {
  background: linear-gradient(135deg, #facc15, #eab308);
}

/* Demo Active Connection */
.rag-connection.demo-active line {
  stroke-width: 1;
  filter: drop-shadow(0 0 3px rgba(250, 204, 21, 0.8));
}

/* Final Result Panel */
.demo-final-result {
  background: linear-gradient(
    135deg,
    rgba(34, 197, 94, 0.15),
    rgba(74, 222, 128, 0.1)
  );
  border: 1px solid rgba(74, 222, 128, 0.3);
  border-radius: 12px;
  padding: 15px;
  margin-top: 15px;
  animation: resultSlideUp 0.5s ease;
}

@keyframes resultSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.demo-final-header {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #4ade80;
  margin-bottom: 12px;
}

.demo-final-query {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 8px;
  padding: 8px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 6px;
}

.demo-final-answer {
  font-size: 12px;
  color: #4ade80;
  line-height: 1.5;
  padding: 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  margin-bottom: 10px;
}

.demo-final-stats {
  display: flex;
  gap: 15px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
}

.demo-final-stats span {
  display: flex;
  align-items: center;
  gap: 4px;
}

/* Disabled state for buttons during demo */
.rag-view-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Node Demo States */
.rag-node.demo-active text:last-child {
  font-weight: 700;
}

.rag-node.demo-visited circle {
  opacity: 0.7;
}
